<template>
  <div class="zoom-selector">
    <div class="selector-content">
      <ul class="zoom-poplist">
        <li
          v-for="(item,index) of options.data"
          :key="index"
          :val="item.value"
          :title="item.text"
          @click="itemClick(item)"
          class="list-item"
        >
          <!--  -->
          <zoom-checkbox v-show="isChecked" :op="checkOp">{{item.text}}</zoom-checkbox>
          {{isChecked ? '' : item.text}}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "zoom-dropdown-content",
  props: {
    options: Object,
    isChecked: Boolean
  },
  data() {
    return {
      checkOp: {
        //  
        Bool: true,
        data: [{ text: "", value: "" }]
      }
    }
  },
  methods: {
    itemClick(e) {
      this.$emit("input", e);
    },
  }
};
</script>