export default {
  props: {
    op: {
      type: Object,
      name: String,
      data: Array,
      Bool: Boolean,
      disabled: Boolean
    }
  },
  data() {
    return {
      active: false,
      disabled: false,
      list: [],
      name: ""
    };
  },
  created() {
    this.reset();
  },
  methods: {
    /**
     * zoom-form
     */
    reset() {
      if (this.op) {
        if (this.op.name) {
          this.name = this.op.name;
        }
        if (this.op.data) {
          this.each(this.op.data);
        }
        if (this.op.disabled) {
          this.disabled = !!this.op.disabled;
        }
      }
    },
    /**
     *  true false
     */
    setDisabled(status) {
      this.disabled = status;
    },
    //   
    load(data) {
      if (data) {
        this.each(data);
      }
    }
  }
}