import { isKorean } from "../common/common.js";
export default {
  props: {
    op: {
      placeHolder: {
        type: String,
        default: null
      },
      hideClose: {  // false
        type: Boolean,
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      },
      readonly: {
        // false
        type: Boolean,
        default: false
      },
      errMsg: { //  
        type: String,
        default: ""
      },
      maxLength: {  //  
        type: Number,
        default: null
      },
      minLength: {  //  
        type: Number,
        default: 0
      },
      width: String,  //   270px
      IconStyle: {
        type: String,
        default: false
      }
    },
    placeholder: {
      type: String,
      default: null
    },
    value: [String, Number]
  },
  data() {
    return {
      currentValue:
        this.value === undefined || this.value === null ? "" : this.value,
      error: false,
      errMsg: null,
      isOnComposition: false,
      valueBeforeComposition: null,
      options: {
        type: "text",
        errMsg: "",
        placeHolder: null,
        width: null,
        maxLength: null,
        minLength: 0,
        hideClose: false,
        disabled: false,
        readonly: false,
        IconStyle: false
      }
    }
  },
  methods: {
    handleChild(e) {
      this.$emit(e);
    },
    /**
     * tab 
     */
    handleTab(e) {
      if (e.keyCode !== 9) return;
      this.handleBlur();
    },
    /**
     *  true false
     */
    setDisabled(status) {
      this.options.disabled = status;
    },
    setCurrentValue(value) {
      // 
      if (this.isOnComposition && value === this.valueBeforeComposition) return;
      this.currentValue = value;
      if (this.isOnComposition) return;
    },
    /**
     * , 
     */
    handleComposition(event) {
      // 
      if (event.type === "compositionend") {
        //  isOnCompositionfalse
        this.isOnComposition = false;
        this.currentValue = this.valueBeforeComposition;
        this.valueBeforeComposition = null;
        //inputinputcompositionendinput
        this.Oninput(event);
      } else {
        //
        const text = event.target.value;
        const lastCharacter = text[text.length - 1] || "";
        //isOnComposition
        this.isOnComposition = !isKorean(lastCharacter);
        if (this.isOnComposition && event.type === "compositionstart") {
          //  valueBeforeComposition
          this.valueBeforeComposition = text;
        }
      }
    },
    // 
    handleBlur() {
      if (this.currentValue.length < this.options.minLength) {
        //  {min} 
        this.errMsg = this.$zoom.$t('input.min', {min: this.options.minLength});
        this.error = true;
        this.$refs["err"].click();
        this.$nextTick(() => {
          this.$refs["err"].click();
          setTimeout(() => {
            this.$nextTick(() => {
              this.error = false;
              $Z(".zoom-tip-container")[0].remove();
            });
          }, 2000);
        });
      } else if (this.options.testing) {
        let test = this.options.testing(this.currentValue);
        if (!test) {
          this.error = true;
          if (this.options.errMsg) {
            this.errMsg = this.options.errMsg;
            this.$nextTick(() => {
              this.$refs["err"].click();
              setTimeout(() => {
                this.$nextTick(() => {
                  this.error = false;
                  $Z(".zoom-tip-container")[0].remove();
                });
              }, 2000);
            });
          }
          return !!test;
        } else {
          this.error = false;
          return true;
        }
      } else {
        return true;
      }
    },
    // ,
    reset() {
      if (!this.options.disabled) {
        this.currentValue = '';
        this.$emit("input", '');
      } else {
        throw new Error(
          // "zoom-ui error: disabled! "
          `${this.$zoom.$t('err.zoom_ui_type')}: ${this.$zoom.$t('err.disabled_clear')}`
        );
      }
    },
    Oninput($event) {
      const value = $event.target.value;
      //
      this.setCurrentValue(value);
      //
      if (this.isOnComposition) return;
      //isOnCompositionfalse
      this.$emit("input", value);
    },
  }
}